<template>
  <el-dialog
    width="1000px"
    title="盘点对照表"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    top="5vh"
  >
    <el-button type="primary" class="printBtn" @click="print">打印</el-button>
    <common-table>
      <template slot="table">
        <el-table
          id="printPage"
          ref="userTable"
          stripe
          :border="true"
          highlight-current-row
          v-loading="isListLoading"
          :data="tableData">
          <el-table-column type="index" label="序号" width="50"></el-table-column>
          <el-table-column prop="storageName" label="库位"></el-table-column>
          <el-table-column prop="productBrand" label="品牌"></el-table-column>
          <el-table-column prop="productModel" label="型号"></el-table-column>
          <el-table-column prop="productName" label="货品名称"></el-table-column>
          <el-table-column prop="productPackFlag" label="包装形式" :formatter="packFormatter"></el-table-column>
          <el-table-column prop="productColor" label="颜色"></el-table-column>
          <el-table-column prop="productStandard" label="规格"></el-table-column>
          <el-table-column prop="remark" label="备注"></el-table-column>
          <!--<el-table-column prop="goodsCount" label="数量"></el-table-column>-->
          <el-table-column prop="productSeq" label="条码" min-width="140"></el-table-column>
          <el-table-column prop="inventoryItemStatus" label="确认结果" width="130">
            <template slot-scope="scope">
              <el-radio-group v-model="scope.row.inventoryItemStatus" size="mini">
                <el-radio label="0">有</el-radio>
                <el-radio label="1">无</el-radio>
              </el-radio-group>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <!--<template slot="pagination">-->
        <!--<el-pagination   background   layout="total, prev, pager, next"-->
                         <!--:current-page.sync="pagination.pageNum"-->
                         <!--:page-size="pagination.pageSize"-->
                         <!--:total="pagination.total"-->
                         <!--@current-change="changePage"-->
        <!--&gt;</el-pagination>-->
      <!--</template>-->
    </common-table>
    <el-row type="flex" justify="end">
      <el-button type="primary" @click="endCheck">结束盘点</el-button>
      <el-button type="primary" @click="save">修改</el-button>
      <el-button @click="cancel">取消</el-button>
    </el-row>
  </el-dialog>
</template>
<script>
  /**
   * StockCheck  Module
   * ----------------------
   * Author: Calamus
   * Date: 2018.09.05
   */
  // validate
  import CommonTable from '@/components/commonTable'
  import CommonWrapper from '@/components/commonWrapper'

  import { stockCheckedtDetail, updateInventoryDetail } from '@/api/warehouse'

  export default {
    name: 'CheckCompare',
    data () {
      return {
        tableData: [],
        isListLoading: false,
        isVisible: false,
        pagination: { // 翻页数据
          pageNum: 1,
          pageSize: 10,
          total: 0
        },
      }
    },
    props: {
      show: {
        type: Boolean,
        default: false
      },
      compareInfo: {
        type: Object,
        deep: true
      }
  },
    watch: {
      show (isShow) {
        this.isVisible = isShow
        if(isShow){
          this.queryList({inventoryUuid:this.compareInfo.inventoryUuid})
        }
      }
    },
    methods: {
      // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getInventoryDetail(searchParams)
    },
      getInventoryDetail (params) {
        this.isListLoading = true
        stockCheckedtDetail(params).then(res => {
          if (!res.error) {
            this.tableData = res
            // this.pagination.total = res.total
            // this.pagination.pageNum = res.pageNum
            // this.pagination.pageSize = res.pageSize
            this.isListLoading = false
          }
        })
      },
      // 列表翻页
      changePage (pageNum) {
        this.pagination.pageNum = pageNum
        this.queryList({inventoryUuid:this.compareInfo.inventoryUuid})
      },
      // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
      packFormatter (row) {
        return row.productPackFlag === '1' ? '有包装' : row.productPackFlag === '2' ? '无包装' : '其他'
      },
      cancel () {
        this.$emit('update:show', false)
        this.isVisible = false
      },
      save () {
        let params = {
          inventoryUuid:this.compareInfo.inventoryUuid,
          inventoryItemList: this.tableData
        }
        updateInventoryDetail(params).then(res => {
          if (!res.error) {
            this.$message({
              message: `修改成功!`,
              type: 'success'
            })
            this.cancel()
          }
        })
      },
      endCheck () {
        let params = {
          inventoryUuid:this.compareInfo.inventoryUuid,
          inventoryItemList: this.tableData,
          type:0
        }
        updateInventoryDetail(params).then(res => {
          if (!res.error) {
            this.$message({
              message: `结束盘点成功!`,
              type: 'success'
            })
            this.cancel()
          }
        })
      },
      print () {
        var headhtml = "<!DOCTYPE html><html style='float:none;'><head><title></title></head><body id='printWindow' style='float:none;line-height: 24px;font-size: 14px;'>";
        let foothtml = "</body></html>";
        let printHtml = document.getElementById('printPage').innerHTML;
        printHtml=headhtml + printHtml + foothtml
        let printer = window.open("",'printPage','height=700,width=750,top=50,left=200');
        printer.document.write(printHtml);
        printer.print();
      }
    },
    components: {
      CommonWrapper,
      CommonTable

    }
  }
</script>
<style lang="scss" scoped>

</style>



